import React from 'react';
import { Link } from 'gatsby-link';
import Helmet from 'react-helmet';
import { Button,
  Header,
  Grid, Container, Form, Input, TextArea, Image } from 'semantic-ui-react';
import FontAwesome from 'react-fontawesome';
import image from '../../assets/images/map-placeholder.png';

export default class Index extends React.Component {
  render() {
    return <div id='contact'>
             <section className='phone'>
               <Container>
                 <Header as='h2' className='heading'>+36 30 219 2311</Header>
                 <Header as='h3'>Kérjen időpontot!</Header>
               </Container>
             </section>
             <section>
               <Container>
                 <Grid stackable columns={ 2 }>
                   <Grid.Column className='contact-info'>
                     <Header as='h1'>Dr. Varga Klára</Header>
                     <div>
                       <FontAwesome name='map-marker'></FontAwesome>
                     </div>
                     <div>
                       1037 Budapest, Montevideo utca 16b
                     </div>
                     <div>
                       <FontAwesome name='phone'></FontAwesome>
                     </div>
                     <div>
                       +36 30 219 2311
                     </div>
                     <div>
                       <FontAwesome name='envelope'></FontAwesome>
                     </div>
                     <div>
                       info@vargaklara.hu
                     </div>
                   </Grid.Column>
                   <Grid.Column className='contact-form'>
                    <Header as='h1'>Kérdése van?</Header>
                     <Form>
                       <Form.Group widths='equal'>
                         <Form.Field control={ Input } label='Teljes név' placeholder='Adja meg teljes nevét' />
                         <Form.Field control={ Input } label='Email cím' placeholder='Adja meg email címét' />
                       </Form.Group>
                       <Form.Field id='form-textarea-control-opinion' control={ TextArea } label='Üzenet' placeholder='Tegye fel kérdését' />
                       <Button primary>Küldés</Button>
                     </Form>
                   </Grid.Column>
                 </Grid>
               </Container>
             </section>
             <section className='map'>
               <Image src={image}></Image>
             </section>
           </div>
  }
}



